@import "vars";

.input {
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  color: #393939;
  padding: 24px;
  background-color: #eee;
  box-sizing: border-box;
  text-transform: uppercase;
  line-height: 1;

  &::placeholder {
    color: inherit;
  }

  &__textarea {
    width: 166px;
  }
}
